#mdEditorContainer {
    min-height: 400px;
    display: flex;
    flex-direction: row;
    width: 100%;
    position: relative;

    .tools {
        position: absolute;
        width: 0;
        border: none;
        right: 0;
        height: auto;
        width: auto;
    }


    > div {
        border: 2px solid #efefef;
        width: 50%;
        overflow: auto;
        white-space: nowrap;
    }

    #mdEditor:focus {
        outline: none;
    }


    ul.md-it-toc {
        list-style: none;
        padding-left: 0;
        margin-top: 16px;
        margin-bottom: 16px;
        margin-left: 16px;


        > li {
            line-height: 1.5;
        }

        ul {
            list-style-type: none;
            padding-left: 16px;
        }
    }
}
